    //         step="0.05"
    //         value={enteredAmount}
    //         onChange={amoutChangeHandler}
    //       />
    //     </div>
    //     <div className="new-expense__control">
    //       <label>Date</label>
    //       <input
    //         type="date"
    //         min="2019-01-01"
    //         max="2022-12-31"
